import React from "react"

const BannerItem = (props) => {
  return (
    <ul>
      {props.item.map((p, i) => {
        return (
          <React.Fragment key={i}>
            <li className="title wow fadeInDown" data-wow-delay="0.2s">
              <div className="img"></div>
              <div className="text">
                <p className="wow fadeInDown" data-wow-duration="1.5s">
                  {p.duration1}
                </p>
                <p className="wow fadeInDown" data-wow-duration="2s">
                  {p.duration2.map((idx) => {
                    return (
                      <React.Fragment>
                        {idx}
                        <br />
                      </React.Fragment>
                    )
                  })}
                </p>
              </div>
            </li>
          </React.Fragment>
        )
      })}
    </ul>
  )
}

export default BannerItem
