import useScript from "react-script-hook/lib/use-script"
import BannerList from "../component/Intro/BannerList"
import CompanyInfo from "../component/Intro/CompanyInfo"

const Intro = () => {
  const [loading, error] = useScript({
    src: `${process.env.PUBLIC_URL}/resources/js/sub.js`,
    // src: "/Layout/styles/js/main.js",
  })
  return (
    <div className="container">
      <div className="content">
        {/* <!-- SUB_VISUAL --> */}
        <div className="visual">
          <div className="sub_visual visual_01">
            <div className="text">
              <p>개요</p>
              <p>
                따뜻한 사람들이 ICT 기술로 행복한 세상을
                <span className="br_640"></span> 만들어가는 곳, 행복ICT
              </p>
            </div>
            <div className="more_btn floating">
              <a href="javascript:">SCROLL</a>
            </div>
          </div>
        </div>
        {/* <!-- // visual --> */}

        {/* <!-- SUB_CONTENTS --> */}
        <div className="sub_contents">
          <div className="info_text">
            <p className="title wow fadeInDown" data-wow-duration="1.5s">
              행복ICT는 기술을 따뜻하게<span className="br_640"></span> 사용할
              줄 아는 기업입니다.
            </p>
            <div className="img wow fadeInDown" data-wow-delay="0.5s">
              <img
                src={`${process.env.PUBLIC_URL}/resources/img/contents/company/info_text1.jpg`}
                alt=""
              />
            </div>
            <p className="text wow fadeInDown" data-wow-delay="0.5s">
              IT가 필요한 곳에 힘이 되고자 하는 목표를 가진 2개의 법인이 통합된
              행복ICT는 공익적 IT서비스를 제공합니다.
              <br />
              <br />
              보다 선한 가치를 창출할 수 있는 사업에 투자를 아끼지 않는 등
              지속가능한 사업 모델을 연구하여 사회적 가치를 더욱 확대하는
              Leading Company의 역할을 구축해 나가고 있습니다. 또한, 취약계층
              고용을 통하여 소외된 이웃들의 사회진입을 도우며, IT 전문가 육성을
              위해 힘쓰고 있습니다.
              <br />
              <br />
              행복ICT는 주어진 역할을 충실히 해내어 함께 살아가는 사회를 만들어
              나가겠습니다.
            </p>
          </div>
          {/* <!-- // info_text --> */}

          <div className="banner_list intro">
            <BannerList />
          </div>
          {/* <!-- // banner_list --> */}

          <div className="with_text">
            <p className="title wow fadeInDown" data-wow-delay="0.4s">
              또한, 행복ICT는{" "}
              <span className="wow fadeInUp" data-wow-delay="0.6s">
                같이
              </span>
              를 아는 기업입니다.
            </p>
            <p className="text wow fadeInDown" data-wow-delay="1s">
              상/하반기 워크샵, 동호회비 지원을 통해 장애인/비장애인이 함께
              Partnership을 다질 수 있도록 지원하며
              <span className="br_1024"></span>더 나아가, ‘HAPPY Friday’를 통해
              가정, 친구와 함께할 수 있는 시간을 늘려
              <span className="br_1024"></span>
              ‘같이’의 가치를 확대하는 행복ICT만의 문화를 만들어갑니다.
            </p>
          </div>
          {/* <!-- // info_text --> */}

          <CompanyInfo />
          {/* <!-- // company_info --> */}
        </div>
        {/* <!-- // SUB_CONTENTS --> */}
      </div>
      {/* <!-- // content --> */}
    </div>
  )
}

export default Intro
