import {
  BrowserView,
  MobileView,
  isMobile,
  isBrowser,
} from "react-device-detect"
import { useEffect, useReducer, useState } from "react"
import { Link } from "react-router-dom"
import useScript from "react-script-hook/lib/use-script"
import Pc from "../component/Main/Device/Pc"
import HappyStory from "../component/Main/HappyStory"
import PortFolio from "../component/Main/PortFolio"
import Banner from "../component/Main/SideBanner"
import Mobile from "../component/Main/Device/Mobile"

const sideBanner = (states, action) => {
  switch (action.type) {
    case "FIRST":
      return (states = 1)
    case "SECOND":
      return (states = 2)
    case "THIRD":
      return (states = 3)
    default:
      return (states = 1)
  }
}

const MainPage = (props) => {
  const [innerWidth, setInnerWidth] = useState(window.innerWidth)

  useEffect(() => {
    const resizeListener = () => {
      setInnerWidth(window.innerWidth)
    }
    window.addEventListener("resize", resizeListener)
  }, [innerWidth])

  let [state, dispatch] = useReducer(sideBanner, 1)
  const first = () => {
    dispatch({
      type: "FIRST",
    })
  }
  const second = () => {
    dispatch({
      type: "SECOND",
    })
  }
  const third = () => {
    dispatch({
      type: "THIRD",
    })
  }

  const [loading, error] = useScript({
    src: `${process.env.PUBLIC_URL}/resources/js/main.js`,
    // src: "/Layout/styles/js/main.js",
  })
  const [play, isPlay] = useState(true)
  return (
    <div className="container">
      <div className="content">
        {/* <!-- VISUAL --> */}
        <section className="section">
          <div className="content_wrapper">
            <div className="visual_wrap">
              <div
                id="main_slide"
                className="visual slick-initialized slick-slider slick-dotted"
                role="toolbar"
              >
                <div aria-live="polite" className="slick-list draggable">
                  <div
                    className="slick-track"
                    role="listbox"
                    style={{
                      opacity: "1",
                      width: innerWidth * 3,
                    }}
                  >
                    <Banner state={state} innerWidth={innerWidth} />
                  </div>
                </div>

                <ul
                  className="slick-dots"
                  role="tablist"
                  style={{ display: "block" }}
                >
                  <li
                    className={state === 1 ? "slick-active" : ""}
                    aria-hidden="false"
                    role="presentation"
                    aria-selected="true"
                    aria-controls="navigation00"
                    id="slick-slide00"
                    onClick={first}
                  >
                    <a>01</a>
                  </li>
                  <li
                    className={state === 2 ? "slick-active" : ""}
                    aria-hidden="false"
                    role="presentation"
                    aria-selected="true"
                    aria-controls="navigation01"
                    id="slick-slide01"
                    onClick={second}
                  >
                    <a>01</a>
                  </li>
                  <li
                    className={state === 3 ? "slick-active" : ""}
                    aria-hidden="false"
                    role="presentation"
                    aria-selected="true"
                    aria-controls="navigation02"
                    id="slick-slide02"
                    onClick={third}
                  >
                    <a>01</a>
                  </li>
                </ul>
              </div>
              {/* <!-- // visual --> */}

              <div className="contact_txt">
                <Link to={"/company/map"}>Contact us</Link>
              </div>
              {/* <!-- // contact_txt --> */}

              <div className="visual_btn">
                <button
                  className={`${!play ? "btn_play on" : "btn_play"}`}
                  onClick={() => isPlay((e) => !e)}
                >
                  {!play ? "재생" : "정지"}
                </button>
              </div>
              {/* <!-- // visual_btn --> */}

              <div className="more_btn floating">
                <a href="javascript:">SCROLL</a>
              </div>
              {/* <!-- // more_btn --> */}
            </div>
            {/* <!-- // visual_wrap --> */}
          </div>
          {/* <!-- // content_wrapper --> */}
        </section>
        {/* <!-- // VISUAL --> */}
        {/* <!-- MENU_AREA --> */}
        <section className="section">
          {/* <BrowserView className="business_wrap">
            <Pc />
          </BrowserView>

          <MobileView className="business_wrap">
            <Mobile />
          </MobileView> */}
          <div className="business_wrap">
            <Pc />
            <Mobile />
          </div>
          {/* <!-- // mobile --> */}
          {/* <!-- // business_wrap --> */}
          {/* <!-- // content_wrapper --> */}
        </section>
        {/* <!-- // MENU_AREA --> */}

        {/* <!-- PORTFOLIO --> */}
        <PortFolio />
        {/* <!-- // PORTFOLIO --> */}

        {/* <!-- HAPPYSTORY --> */}
        <HappyStory />
        {/* <!-- // HAPPYSTORY --> */}
      </div>
      {/* <!-- // content --> */}
    </div>
  )
}

export default MainPage
